import React from 'react';

export const Button = (props) => {
    const clickEvents = props.clickEvents | null;
    return <span className="btn-wrapper"><button className={props.class} onClick={props.clickEvents}>{props.value}</button></span>
}

export const Textarea = (props) => {
    return <textarea rows={props.rows} name={props.name} onChange={props.onChangeEvent} id={props.id} className={props.class} ></textarea>
}

export const Checkbox = (props) => {
    return <label htmlFor={props.id} ><a href="" className={props.class} onClick={props.clickEvents} value={props.value} id={props.id} />{props.value}</label>
}

