import React, { Fragment } from 'react';
import SpatialNavigation from '../spatial_navigation';
class Dropdown extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            ddOpen: false,
            topScrollValue: 0
        }
    }
    dropDownClick = (e) => {
        e.preventDefault();
        this.setState({
            ddOpen: !this.state.ddOpen
        })

    }
    clickOption = (e, id, value, ind, nextSelectedItem) => {
        SpatialNavigation.focus(nextSelectedItem, true);
        e.preventDefault();
        this.setState({
            ddOpen: false
        }, () => {
            this.props.clickDropdown(ind, value, this.props.dropDownList);
        })
    }

    componentDidMount() {
        document.addEventListener('keydown', this.keyPressEvent);
    }
    keyPressEvent = (event) => {
        var keyCode = event.keyCode;
        switch (keyCode) {
            case 38:

                return;
            case 40:
                this.downScrollContainer();
                return;
            default:
                return;
        }
    }
    downScrollContainer = () => {
        // if (this.state.ddOpen === true && this.props.dropDownList.data.length > 2) {
        //     this.setState({
        //         topScrollValue: this.state.topScrollValue - 38
        //     })
        // }
    }
    render() {
        const hrefNone = '#';
        const { dropDownList, selectedValue } = this.props;
        const ddSelectedValue = selectedValue ? selectedValue : dropDownList.data[0].value;
        const ddOpen = this.state.ddOpen ? 'block' : 'none';
        const scrollStyle = {
            WebkitTransform: `translate3d(0,${this.state.topScrollValue}px,0)`
        }
        return (
            <Fragment>
                <label>{dropDownList.name}</label>
                <a href={hrefNone} className="focusable selectlist" onClick={(e) => this.dropDownClick(e)} ><span id="dd-1-value">{ddSelectedValue}</span>	 <i className="down"></i>
                </a>
                <div id={dropDownList.id} className="ddmenu" style={{ "display": ddOpen }}>
                    <div className='scrollContainer' style={scrollStyle} >
                        {dropDownList.data && dropDownList.data.map((item, i) => {
                            return <a href={hrefNone} key={i} id={item.id} className="focusable" onClick={(e) => this.clickOption(e, item.id, item.value, i, this.props.nextSelectedItem)}>{item.value}</a>
                        })}
                    </div>
                </div>
            </Fragment>
        )
    }
}
export default Dropdown;