import React from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
import $ from "jquery";
import SpatialNavigation from './spatial_navigation';
import { Button, Textarea, Checkbox } from './Components/HTMLElements';
import Dropdown from './Components/Dropdown';
require('./FeedbackForm.css');
const checkboxlist = {
    checkbox1: {
        id: "checkbox1",
        value: "Checkbox 1",
        checked: false
    },
    checkbox2: {
        id: "checkbox2",
        value: "Checkbox 2",
        checked: false
    },
    checkbox3: {
        id: "checkbox3",
        value: "Checkbox 3",
        checked: false
    },
    checkbox4: {
        id: "checkbox4",
        value: "Checkbox 4",
        checked: false
    }
}
const radioList = {
    radio1: {
        id: "radio1",
        value: "Radio 1",
        checked: false
    },
    radio2: {
        id: "radio2",
        value: "Radio 2",
        checked: false
    },
    radio3: {
        id: "radio3",
        value: "Radio 3",
        checked: false
    },
    radio4: {
        id: "radio4",
        value: "Radio 4",
        checked: false
    }
}
const dropDownList = {
    name: "Select Feedback 1",
    id: "feedback1",
    data: [{
        id: "dd1",
        value: "List 1",
        selected: false,
        childrenDdMenu: {
            name: "Label Sub Dropdown 11",
            id: "subdd1",
            data: [{
                id: "subdata-11",
                value: "Sub List 11",
                selected: false,
            },
            {
                id: "subdata-12",
                value: "Sub List 12",
                selected: false,
            },
            {
                id: "subdata-13",
                value: "Sub List 13",
                selected: false,
            }]
        }
    },
    {
        id: "dd2",
        value: "List 2",
        selected: false,
        childrenDdMenu: {
            name: "Label Sub Dropdown 22",
            id: "subdd2",
            data: [{
                id: "subdata-21",
                value: "Sub List 21",
                selected: false,
            },
            {
                id: "subdata-22",
                value: "Sub List 22",
                selected: false,
            },
            {
                id: "subdata-23",
                value: "Sub List 23",
                selected: false,
            }]
        }
    }]
};
class FeedbackForm extends React.Component {
    constructor() {
        super();
        this.state = {
            checkboxList: checkboxlist,
            radioList: radioList,
            dropDownList: dropDownList,
            subDdList: null,
            name: '',
            description: '',
            sortDesc: '',
            feedbackOption: '',
            subMenuValue: '',
            topScrollValue:0
        }
    }

    componentDidMount() {
        SpatialNavigation.init();
        SpatialNavigation.add({
            selector: '.focusable'
        });
        SpatialNavigation.makeFocusable();
        SpatialNavigation.focus();

        
    }

    

    

    handleChange = (e) => {
        let { name, value } = e.target;
        this.setState({
            [name]: value
        })
    }
    getCheckboxData = (checkboxList) => {
        const checkedList = Object.keys(checkboxList).filter((val) => {
            return checkboxList[val].checked === true;
        })

        return checkedList;
    }
    getRadioData = (radiolist) => {
        const selectedRadio = Object.keys(radioList).filter((val) => {
            return radioList[val].checked === true;
        })
        return selectedRadio[0]
    }

    submitForm = (e) => {
        e.preventDefault();
        const data = this.state;
        const payload = {
            name: data.name,
            feedbackOption: data.feedbackOption,
            subMenuValue: data.subMenuValue,
            description: data.description,
            checkbox: this.getCheckboxData(data.checkboxList),
            radio: this.getRadioData(data.radioList)
        }
        const url = 'https://feedback-39e94.firebaseio.com/feedback.json';
        var self = this;
        axios.post(url, JSON.stringify(payload))
            .then((response) => {
                if (response.status === 200) {
                    self.setState({
                        name: '',
                        feedbackOption: '',
                        subMenuValue: '',
                        description: '',
                        checkboxList: checkboxlist,
                        radioList: radioList
                    })
                }
            })
            .catch((error) => {
                console.log(error)
            })

    }

    checkboxClick = (e, id, ind) => {
        const checkStateList = { ...this.state.checkboxList };
        checkStateList[id].checked = checkStateList[id].checked ? false : true;
        this.setState({
            checkboxList: checkStateList
        });
        e.preventDefault();
    }

    radioClick = (e, id, ind) => {
        let radioList = { ...this.state.radioList };
        for (let a in radioList) {
            if (radioList[a].id === id) {
                radioList[a].checked = true;
            } else {
                radioList[a].checked = false;
            }
        }
        this.setState({
            radioList: radioList
        });
        e.preventDefault();
    }

    selectedDropdown = (ind, value, ddData) => {
        const submenuData = { ...this.state.dropDownList.data };
        let isSubmenu = false;
        let i;
        for (i in ddData.data); {
            isSubmenu = ddData.data[i].childrenDdMenu ? true : false
        }
        if (isSubmenu) {
            this.setState({
                ...this.state,
                feedbackOption: value,
                subDdList: submenuData[ind].childrenDdMenu,
                subMenuValue: ''
            })
        } else {
            this.setState({
                ...this.state,
                subMenuValue: value
            })
        }
    }

    render() {
        const scrollStyle = {
            WebkitTransform:`translate3d(0,0px,0)`
        }
        //  WebkitTransform:`translate3d(0,${this.state.topScrollValue}px,0)`
        return (
            <div className="container form" id="container" ref="container">
                <h1>Feedback Form</h1>
                <div id="output"></div>
                <div id="formScroller" style={scrollStyle}>
                <form onSubmit={this.submitForm}>
                    <div className="row"><div id="form-submitted-msg"></div></div>
                    <div className="row">
                        <label>Name</label>
                        <input type="text" style={{ 'background': 'lightblue' }} className="focusable" name="name" value={this.state.name} onChange={this.handleChange} id="name" />
                    </div>
                   
                    <div className="row" id="ddcontainer1">
                        {
                            this.state.dropDownList && <Dropdown
                                nextSelectedItem={'#ddcontainer1 .focusable'}
                                selectedValue={this.state.feedbackOption}
                                dropDownList={this.state.dropDownList}
                                clickDropdown={this.selectedDropdown} />
                        }

                    </div>
                    {this.state.subDdList &&
                        <div className="row" id="ddcontainer2">
                            {
                                this.state.subDdList && <Dropdown
                                    nextSelectedItem={'#ddcontainer2 .focusable'}
                                    selectedValue={this.state.subMenuValue}
                                    dropDownList={this.state.subDdList}
                                    clickDropdown={this.selectedDropdown} />
                            }

                        </div>
                    }
                    <div className="row">
                        <label>Descriptions</label>
                        <Textarea
                            rows={'4'}
                            id={'id1'}
                            name={'description'}
                            value={this.state.description}
                            onChangeEvent={this.handleChange}
                            class={'focusable'}></Textarea>
                    </div>
                    <div className="row checkbox-container">
                        {
                            this.state.checkboxList && Object.keys(this.state.checkboxList).map((key, ind) => {
                                const eleData = this.state.checkboxList[key];
                                return <Checkbox
                                    id={eleData.id}
                                    key={key + ind}
                                    clickEvents={(e) => this.checkboxClick(e, eleData.id, ind)}
                                    class={eleData.checked ? 'focusable checkbox checked' : 'focusable checkbox'}
                                    value={eleData.value} />
                            })
                        }
                    </div>
                    <div className="row checkbox-container">
                        {
                            this.state.radioList && Object.keys(this.state.radioList).map((key, ind) => {
                                const eleData = this.state.radioList[key];
                                return <Checkbox
                                    id={eleData.id}
                                    key={key + ind}
                                    clickEvents={(e) => this.radioClick(e, eleData.id, ind)}
                                    class={eleData.checked ? 'focusable radio selected' : 'focusable radio'}
                                    value={eleData.value} />
                            })
                        }
                    </div>
                    <div className="row">
                        <Button class={'focusable'} value={'Submit'} clickEvents={this.submitForm}></Button>
                        {' '} <Button class={'focusable'} value={'Clear'}></Button></div>
                </form>
                </div>
            </div>
        )
    }
}

export default FeedbackForm;